import { useGetUsersOrdersQuery } from "../generated/graphql";
import { useLocation } from "react-router-dom";

interface UserOrders {
    products: [
        {
            product_id: number;
            name: string;
            desc: string;
            price: number;
            stock: number;
            exp_date?: string;
            quantityOrdered: number;
            productSubtotal: number;
            images: [
                {
                    img_id: number;
                    img_url: string;
                    index: number;
                }
            ];
        }
    ];
    order_total: number;
    order_id: number;
    tracking_num?: string;
    coupon: string;
    discount: string;
    date_of_purchase: string;
}

const UsersOrders = () => {
    const location: any = useLocation();

    if (!!location.state) {
        if (location.state.reload) {
            window.location.reload();
        }
    }

    const { data, loading } = useGetUsersOrdersQuery();

    if (loading) {
        return <></>;
    }

    let tmp: [UserOrders] = data?.getUsersOrders.reverse() as any;

    return (
        <>
            {!!tmp ? (
                <>
                    <div className="container">
                        <div
                            style={{
                                display: "flex",
                                justifyContent: "center",
                            }}
                        >
                            <h3>Your Purchases</h3>
                        </div>
                        {tmp.map((_val, i) => {
                            let amountOff = Number(
                                (tmp[i].order_total *
                                    Number("0." + tmp[i].discount)) /
                                    100
                            ).toFixed(2);

                            let str, dateOfPurchase;
                            if (tmp[i].date_of_purchase) {
                                str = tmp[i].date_of_purchase.split(" ");
                                dateOfPurchase = `${str[0]} ${str[1]} ${str[2]} ${str[3]}`;
                            }

                            return (
                                <div className="row z-depth-1" key={i}>
                                    <div
                                        className="col s12"
                                        style={{ padding: "0px" }}
                                    >
                                        <div
                                            className="card-header row"
                                            style={{
                                                borderLeft: "5px solid #ff0000",
                                                paddingTop: "13px",
                                                paddingBottom: "13px",
                                                paddingRight: "25px",
                                                paddingLeft: "20px",
                                                margin: "0px",
                                                zIndex: 4,
                                                position: "relative",
                                            }}
                                        >
                                            <span
                                                className="hide-on-small-only"
                                                style={{
                                                    display: "inline-block",
                                                    width: "41.7%",
                                                }}
                                            >
                                                <span
                                                    style={{
                                                        color: "#1d252c",
                                                        paddingRight: "1rem",
                                                        fontWeight: 700,
                                                    }}
                                                >
                                                    Order ID
                                                </span>
                                                <span>
                                                    CR-{tmp[i].order_id}
                                                </span>
                                            </span>

                                            <span
                                                className="hide-on-med-and-up"
                                                style={{
                                                    display: "inline-block",
                                                    width: "50%",
                                                }}
                                            >
                                                <span
                                                    style={{
                                                        color: "#1d252c",
                                                        paddingRight: "1rem",
                                                        fontWeight: 700,
                                                    }}
                                                >
                                                    Order ID
                                                </span>
                                                <span>
                                                    CR-{tmp[i].order_id}
                                                </span>
                                            </span>

                                            <span
                                                style={{
                                                    display: "inline-block",
                                                    width: "17%",
                                                }}
                                                className="hide-on-med-and-down"
                                            >
                                                {tmp[i].date_of_purchase ? (
                                                    <span>
                                                        {dateOfPurchase}
                                                    </span>
                                                ) : (
                                                    <></>
                                                )}
                                            </span>

                                            <span
                                                style={{
                                                    display: "inline-block",
                                                    width: "30.3%",
                                                    fontWeight: 700,
                                                }}
                                            >
                                                <span
                                                    style={{
                                                        display: "flex",
                                                        justifyContent:
                                                            "space-between",
                                                    }}
                                                >
                                                    <span>
                                                        $
                                                        {Number(
                                                            tmp[i].order_total /
                                                                100
                                                        ).toFixed(2)}{" "}
                                                        {tmp[i].discount ? (
                                                            <span>
                                                                (
                                                                <span
                                                                    style={{
                                                                        color:
                                                                            "rgb(255, 0, 0)",
                                                                    }}
                                                                >
                                                                    -$
                                                                    {amountOff}
                                                                </span>
                                                                )
                                                            </span>
                                                        ) : (
                                                            <></>
                                                        )}
                                                    </span>
                                                    <span>
                                                        {/* <a href="">
                                                            See details{" "}
                                                             <i className="material-icons">
                                                        chevron_right
                                                    </i> 
                                                        </a> */}
                                                    </span>
                                                </span>
                                            </span>
                                        </div>

                                        <div className="divider"></div>

                                        <div
                                            className="card-body"
                                            style={{
                                                backgroundColor: "white",
                                                zIndex: 2,
                                                position: "relative",
                                            }}
                                        >
                                            {tmp[i].products.map((_val, j) => {
                                                return (
                                                    <div
                                                        className="row"
                                                        key={j}
                                                    >
                                                        <div className="col s12 m4">
                                                            <img
                                                                style={{
                                                                    width:
                                                                        "100%",
                                                                }}
                                                                src={
                                                                    tmp[i]
                                                                        .products[
                                                                        j
                                                                    ].images[0]
                                                                        .img_url
                                                                }
                                                            />
                                                        </div>
                                                        <div
                                                            style={{
                                                                display: "flex",
                                                                flexDirection:
                                                                    "column",
                                                                justifyContent:
                                                                    "space-between",
                                                                height: "100%",
                                                                paddingLeft: 0,
                                                            }}
                                                            className="col m2 offset-m1 hide-on-small-only"
                                                        >
                                                            <p
                                                                className="flow-text"
                                                                style={{
                                                                    fontSize:
                                                                        "16px",
                                                                    fontWeight:
                                                                        "bold",
                                                                }}
                                                            >
                                                                {
                                                                    tmp[i]
                                                                        .products[
                                                                        j
                                                                    ].name
                                                                }
                                                            </p>
                                                            <p className="hide-on-small-only">
                                                                Qty:{" "}
                                                                {
                                                                    tmp[i]
                                                                        .products[
                                                                        j
                                                                    ]
                                                                        .quantityOrdered
                                                                }{" "}
                                                                ($
                                                                {Number(
                                                                    tmp[i]
                                                                        .products[
                                                                        j
                                                                    ]
                                                                        .productSubtotal /
                                                                        100
                                                                ).toFixed(2)}
                                                                )
                                                            </p>
                                                        </div>

                                                        <div
                                                            style={{
                                                                display: "flex",
                                                                justifyContent:
                                                                    "center",
                                                            }}
                                                            className="col s12 hide-on-med-and-up"
                                                        >
                                                            <p
                                                                className="flow-text"
                                                                style={{
                                                                    fontSize:
                                                                        "16px",
                                                                    fontWeight:
                                                                        "bold",
                                                                }}
                                                            >
                                                                {
                                                                    tmp[i]
                                                                        .products[
                                                                        j
                                                                    ].name
                                                                }
                                                            </p>
                                                        </div>
                                                        {/* <div className="col s5 offset-s1">
                                                            <p>
                                                                Qty:{" "}
                                                                {
                                                                    tmp[i]
                                                                        .products[
                                                                        j
                                                                    ]
                                                                        .quantityOrdered
                                                                }{" "}
                                                                ($
                                                                {Number(
                                                                    tmp[i]
                                                                        .products[
                                                                        j
                                                                    ]
                                                                        .productSubtotal /
                                                                        100
                                                                ).toFixed(2)}
                                                                )
                                                            </p>
                                                        </div> */}
                                                        <div className="col offset-m1 m3 hide-on-small-only">
                                                            {!tmp[i]
                                                                .tracking_num ? (
                                                                <>
                                                                    <p
                                                                        style={{
                                                                            fontWeight: 700,
                                                                        }}
                                                                    >
                                                                        Order is
                                                                        being
                                                                        proccessed
                                                                    </p>
                                                                    <div
                                                                        style={{
                                                                            width:
                                                                                "100%",
                                                                            borderBottom:
                                                                                " 4px solid #ff0000",
                                                                        }}
                                                                    ></div>
                                                                </>
                                                            ) : (
                                                                <>
                                                                    <p
                                                                        style={{
                                                                            fontWeight: 700,
                                                                        }}
                                                                    >
                                                                        Shipped{" "}
                                                                        =&gt;{" "}
                                                                        <a
                                                                            href={`https://www.google.com/search?q=${tmp[i].tracking_num}`}
                                                                        >
                                                                            {
                                                                                tmp[
                                                                                    i
                                                                                ]
                                                                                    .tracking_num
                                                                            }
                                                                        </a>
                                                                    </p>
                                                                    <div
                                                                        style={{
                                                                            width:
                                                                                "100%",
                                                                            borderBottom:
                                                                                " 4px solid green",
                                                                        }}
                                                                    ></div>
                                                                </>
                                                            )}
                                                        </div>

                                                        <div
                                                            className="col s12 hide-on-med-and-up"
                                                            style={{}}
                                                        >
                                                            {!tmp[i]
                                                                .tracking_num ? (
                                                                <>
                                                                    <p
                                                                        style={{
                                                                            fontWeight: 700,
                                                                            display:
                                                                                "flex",
                                                                            justifyContent:
                                                                                "center",
                                                                            marginBottom:
                                                                                "32px",
                                                                        }}
                                                                    >
                                                                        <span
                                                                            style={{
                                                                                borderLeft:
                                                                                    " 4px solid #ff0000",
                                                                                paddingLeft:
                                                                                    "10px",
                                                                            }}
                                                                        >
                                                                            Order
                                                                            is
                                                                            being
                                                                            proccessed
                                                                        </span>
                                                                    </p>
                                                                </>
                                                            ) : (
                                                                <>
                                                                    <p
                                                                        style={{
                                                                            fontWeight: 700,
                                                                            display:
                                                                                "flex",
                                                                            justifyContent:
                                                                                "center",
                                                                            marginBottom:
                                                                                "32px",
                                                                        }}
                                                                    >
                                                                        <span
                                                                            style={{
                                                                                borderLeft:
                                                                                    " 4px solid #008000",
                                                                                paddingLeft:
                                                                                    "10px",
                                                                            }}
                                                                        >
                                                                            Shipped{" "}
                                                                            =&gt;{" "}
                                                                            <a
                                                                                href={`https://www.google.com/search?q=${tmp[i].tracking_num}`}
                                                                            >
                                                                                {
                                                                                    tmp[
                                                                                        i
                                                                                    ]
                                                                                        .tracking_num
                                                                                }
                                                                            </a>
                                                                        </span>
                                                                    </p>
                                                                </>
                                                            )}
                                                            {tmp[i].products
                                                                .length > 1 &&
                                                            tmp[i].products
                                                                .length -
                                                                1 !==
                                                                j ? (
                                                                <div
                                                                    className="divider"
                                                                    style={{
                                                                        marginLeft:
                                                                            "10%",
                                                                        width:
                                                                            "80%",
                                                                    }}
                                                                ></div>
                                                            ) : (
                                                                <></>
                                                            )}
                                                        </div>
                                                    </div>
                                                );
                                            })}
                                        </div>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </>
            ) : (
                <>
                    <div style={{ display: "flex", justifyContent: "center" }}>
                        <h1>You haven't ordered anything yet</h1>
                    </div>
                </>
            )}
        </>
    );
};

export default UsersOrders;
