import "../css/Preloader.scss";
import anime from "animejs/lib/anime.es.js";
import { useEffect } from "react";

const Preloader = () => {
    useEffect(() => {
        anime({
            targets: [".circle-1"],
            translateY: -24,
            direction: "alternate",
            loop: true,
            elasticity: 400,
            easing: "easeInOutElastic",
            duration: 1600,
            delay: 800,
        });

        anime({
            targets: [".circle-2"],
            translateY: 24,
            direction: "alternate",
            loop: true,
            elasticity: 400,
            easing: "easeInOutElastic",
            duration: 1600,
            delay: 800,
        });

        anime({
            targets: [".circle-3 "],
            translateY: -24,
            direction: "alternate",
            loop: true,
            elasticity: 400,
            easing: "easeInOutElastic",
            duration: 1600,
            delay: 800,
        });

        anime({
            targets: [".circle-4"],
            translateY: 24,
            direction: "alternate",
            loop: true,
            elasticity: 400,
            easing: "easeInOutElastic",
            duration: 1600,
            delay: 800,
        });
    });

    return (
        <div className="loadingWrapper">
            <div className="absolute-center">
                <div className="loader">
                    <div className="circle circle-1"></div>
                    <div className="circle circle-2"></div>
                    <div className="circle circle-3"></div>
                    <div className="circle circle-4"></div>
                </div>
            </div>
        </div>
    );
};

export default Preloader;
