import CopyrightBar from "./CopyrightBar";
import FooterLogo from "../../images/Blank_Footer_logo.png";
import { FaInstagram, FaEnvelope } from "react-icons/fa";
import "../../css/footer.scss";

const Footer = () => {
    return (
        <span>
            <footer
                className="noselect"
                style={{
                    backgroundColor: "#0a0a0a",
                    width: "100%",
                    height: "280px",
                }}
            >
                <div className="container">
                    <div
                        className="row"
                        style={{
                            width: "100%",
                            display: "flex",
                            justifyContent: "space-between",
                        }}
                    >
                        <div className="col l6 s12">
                            <img
                                src={FooterLogo}
                                alt="footer_logo"
                                style={{ height: "280px" }}
                            />
                        </div>

                        <div className="col l4 offset-12 s12">
                            <ul id="footer-info">
                                <li>
                                    <a
                                        href="#/shipping_policy"
                                        className="white-text"
                                    >
                                        <h4 style={{ fontWeight: 600 }}>
                                            Shipping Policy
                                        </h4>
                                    </a>
                                </li>
                                <li>
                                    <a href="#/contact" className="white-text">
                                        <h4
                                            style={{
                                                marginTop: "0px",
                                                fontWeight: 600,
                                            }}
                                        >
                                            Contact Us
                                        </h4>
                                    </a>
                                </li>
                                <li>
                                    <a href="#/FAQ" className="white-text">
                                        <h4
                                            style={{
                                                marginTop: "0px",
                                                fontWeight: 600,
                                            }}
                                        >
                                            FAQ
                                        </h4>
                                    </a>
                                </li>
                                <li>
                                    <a
                                        id="social-link"
                                        href="https://www.instagram.com/cantrelate.jp/"
                                    >
                                        <FaInstagram
                                            style={{
                                                width: "30px",
                                                height: "30px",
                                                marginRight: "7px",
                                            }}
                                        />
                                    </a>
                                    <a
                                        id="social-link"
                                        href="mailto:cantrelatejp@gmail.com"
                                    >
                                        <FaEnvelope
                                            style={{
                                                width: "30px",
                                                height: "30px",
                                            }}
                                        />
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </footer>
            <CopyrightBar />
        </span>
    );
};

export default Footer;
